.nav-wrapper{
    background-color: #373737 !important;
    font-family: 'Raleway', sans-serif;
   }
  
  /* Styling Links*/
  
  /* Unvisited link */
  a:link {
      color: #f4f4f4";
    }
    
  /* visited link */
    a:visited {
      color: #f4f4f4";
    }
    
  /* mouse over link */
    a:hover {
      color: #f4f4f4";
    }
    
  /* selected link */
    a:active {
      color: #f4f4f4";
    }
  
    a:link {
      text-decoration: none;
    }
    
    a:visited {
      text-decoration: none;
    }
    
    a:hover {
      text-decoration: none;
    }
    
    a:active {
      text-decoration: none;
    }
  